@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:ital,wght@0,400;0,500;1,400;1,500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:ital,wght@0,400;0,500;1,400;1,500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:ital,wght@0,400;0,500;1,400;1,500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:ital,wght@0,400;0,500;1,400;1,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.home_container{display:flex;align-items:center}.App{display:flex;flex-direction:column;background-color:#090909;color:#fff;width:100vw;height:100vh;top:0}

.header{width:100%;display:flex;flex-direction:row;justify-content:space-between}.hamburger__menu{cursor:pointer;padding:10px}.router__nav{display:flex;flex-direction:row;font-family:"IBM Plex Mono",monospace}.router__nav ul{display:flex;flex-direction:row;list-style-type:none}.router__nav ul li{padding-left:10px;padding-right:10px}.nav__link{text-decoration:none;color:#fff}.nav__link__active{color:#fff;text-decoration:underline;-webkit-text-decoration-style:double;text-decoration-style:double}

.body__container{display:flex;flex-direction:row;justify-content:space-between;align-items:center;padding:20px;height:100vh}.body__container .text{object-fit:cover;display:flex;justify-content:center;font-family:"IBM Plex Mono",monospace;flex-direction:column;align-content:center}.body__container .text .hello__world{color:gray;font-size:large}.body__container .text .pitch{font-size:xx-large}.body__container .text .contact__button{color:#ccc;justify-content:flex-start;font-family:"IBM Plex Mono",monospace;white-space:normal;border:solid 1px white;margin-top:10px}.body__container .image{display:flex;width:30%;justify-content:center;align-items:center}.body__container .image .developer_image{width:100%;-webkit-filter:invert(96.6%);filter:invert(96.6%)}

.about__Container{color:#fff}

