@import "./../../style/theme.scss";
.header {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.hamburger__menu {
  cursor: pointer;
  padding: 10px;
}

.router__nav {
  display: flex;
  flex-direction: row;
  font-family: $font;
  ul {
    display: flex;
    flex-direction: row;
    list-style-type: none;
    li {
      padding-left: 10px;
      padding-right: 10px;
    }
  }
}
.nav__link {
  text-decoration: none;
  color: $foreground_color;
}
.nav__link__active {
  color: $foreground_color;
  text-decoration: underline;
  text-decoration-style: double;
}
